body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin: 0; padding: 0; box-sizing: border-box; color: #fff; font-family: 'Noto Sans KR', sans-serif;}
*:after,*:before {box-sizing: border-box;}
ul, li, a{text-decoration: none; list-style:none;}
body{ -ms-overflow-style: none; width: 100%; height: 100%;}
::-webkit-scrollbar { display: none;}

b{color: #333; font-weight: 500;}
.c{color: #f4a600 !important;}
.cr{color: #b31313 !important;}

/* 버튼 */
.about-resume{
  width: 200px;
  margin: 0 20px 0 0;
  padding: 12px 0;
  color: #333;
  font-size: 15px;
  border: 1px solid #ccc;
  border-radius: 6px; background-color: #fff;
  transition: all 1s;
  cursor:  pointer;  
  display: flex; justify-content: center; align-items: center; 
}

.about-resume:hover{
  background-color: #eee;
  border: 1px solid #eee;
  transition: all 1s;
}

/* HEADER */

.header{
  position: absolute; top: 0; left: 50%; -webkit-transform: translateX(-50%); transform: translateX(-50%); z-index: 9999;
  width: 90%; min-height: 130px; 
  display: flex; justify-content: flex-end; align-items: center;
}

.header > .mainIcon{
  width: 35px; height: 35px; border-radius: 50%;
  overflow: hidden;
}

.header > .mainIcon img{
  width: 35px; height: 35px;
}

.header .hmenu{
  width: 35px; height: 30px;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
  z-index: 9999;
}

.hmenu span{
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  border-radius: 4px;
  background: #fff;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition: .25s ease-in-out;
}

.hmenu span:nth-child(1) {
  top: 0px;
  -webkit-transform-origin: left center;
          transform-origin: left center;
}

.hmenu span:nth-child(2) {
  top: 15px;
  -webkit-transform-origin: left center;
          transform-origin: left center;
}

.hmenu span:nth-child(3) {
  top: 30px;
  -webkit-transform-origin: left center;
          transform-origin: left center;
}

.hmenu.open span{
  background: #fff !important;
  opacity: 0.5;
}

.hmenu.open span:nth-child(1) {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  top: 0px;
  left: 5px;
}

.hmenu.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

.hmenu.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  top: 25px;
  left: 5px;
}

/* HEADER MENU */

.hide-menu{
  width: 60%; height: 100vh;
  background-color: #111;
  text-align: left;
  display: flex; align-items: center; justify-content: center; flex-direction: column;
  position: absolute; top: 0; right: -100%; z-index: 9998;
  transition: all 0.5s;
  visibility: hidden;
}

.show{
  right: 0;
  visibility: visible;
  transition: all 0.5s;
}

.hide-menu ul{
  transition: all 0.3s ease-in-out;
  color: #fff;
  width: 80%;
}

.hide-menu ul:nth-child(1) > li{
  display: flex; flex-direction: column;
  height: 110px; overflow: hidden; color: #fff;
  transition: 0.5s;
}

.hide-menu ul:nth-child(1) li p a{
  color: #fff; font-family: 'Rozha One', serif; font-size: 80px;
  display: block;
  height: 110px;
}

.hide-menu ul:nth-child(1) > li:hover p:nth-child(1){
  margin-top: -110px;
  transition: 0.5s;
}

.hide-menu ul:nth-child(1) > li p:nth-child(2) a{
  color: #f4a600 !important; 
}

.hide-menu ul:nth-child(2):before{
  content: ''; display: block;
  border: 1px solid #fff;
  margin: 80px 0 40px;
}

.hide-menu > ul:nth-child(2) > li{
  display: flex;
}

.hide-menu > ul > li > div{
  margin: 0 60px 35px 0;
}

.hide-menu > ul:nth-child(2) > li > p{
  opacity: 0.5; margin-bottom: 5px;
}

.tel > p , .email > p{
  opacity: 0.5;
}

.icon p{
  width: 25px; height: 25px; display: inline-block;
  margin-right: 25px;
}

.icon p img{
  width: 100%; height: 100%;
}

/* FOOTER */

footer{
  width: 100%; height: 300px;
  background-color: #111;
  display: flex; align-items: center;
}

.footer{
  width: 90%;
  position: relative; left: 50%; -webkit-transform: translateX(-50%); transform: translateX(-50%);
}

.left{
  display: inline-block;
}

.left ul:nth-child(1){
  transition: all 0.3s ease-in-out;
  color: #fff;
  display: flex; 
}

.left ul:nth-child(1) > li{
  display: flex; flex-direction: column;
  height: 60px; overflow: hidden; color: #fff;
  transition: 0.5s;
}

.left ul:nth-child(1) > li:nth-child(2){
  margin: 0 70px;
}

.left ul:nth-child(1) li p a{
  color: #fff; font-family: 'Rozha One', serif; font-size: 40px;
  display: block;
  height: 60px;
}

.left ul:nth-child(1) > li:hover p:nth-child(1){
  margin-top: -60px;
  transition: 0.5s;
}

.left ul:nth-child(1) > li p:nth-child(2) a{
  color: #f00 !important; 
}

.left ul:nth-child(2):before{
  content: ''; display: block;
  border: 1px solid #fff;
  margin: 20px 0;
}

.left > ul:nth-child(2) > li{
  display: flex;
}

.left > ul > li > div{
  margin: 0 60px 35px 0;
}

.left > ul:nth-child(2) > li > p{
  opacity: 0.5; margin-bottom: 5px;
}

/* MAIN */

.main{
  width: 90%; height: 100vh;
  position: absolute; top: 0; left: 50%; -webkit-transform: translateX(-50%); transform: translateX(-50%);
}

.main_title{
  font-size: 80px;
  font-weight: 300;
  color: #111;
  display: inline-block; 
  position: relative; top: 50%; -webkit-transform: translateY(-50%); transform: translateY(-50%);
}

.message1{
  height: 120px;
  overflow: hidden;
}

strong{
  color: #111;
  font-weight: 500;
}

.word{
  color: #f4a600;
}

.word:nth-child(1){
  -webkit-animation: show_tit 5s ease-in-out infinite both;
          animation: show_tit 5s ease-in-out infinite both;
}

@-webkit-keyframes show_tit {
  0% {margin-top:-0px;}
  5% {margin-top:-120px;}
  33% {margin-top:-120px;}
  38% {margin-top:-240px;}
  66% {margin-top:-240px;}
  71% {margin-top:-360px;}
  99.99% {margin-top:-360px;}
  100% {margin-top:-0px;}
}

@keyframes show_tit {
  0% {margin-top:-0px;}
  5% {margin-top:-120px;}
  33% {margin-top:-120px;}
  38% {margin-top:-240px;}
  66% {margin-top:-240px;}
  71% {margin-top:-360px;}
  99.99% {margin-top:-360px;}
  100% {margin-top:-0px;}
}

/* CANVAS */

canvas{
  width: 100%; height: 100vh;
  overflow: hidden;
}

.chat{
  position: absolute; bottom: 95px; right: 120px;
  text-align: center; border-radius: 15px; font-size: 15px;
  padding: 10px 15px;
  background-color: #fff; color: #000;
  font-family: 'Noto Sans KR', sans-serif;
}

.chat:before{
  content: '';
  position: absolute;
  right:15px;
  bottom: -8px;
  margin-left: -15px;
  border-top: 15px solid #fff;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
}

/* 낮밤 토글 버튼 */
input label{
  transition: all 0.5s;
}

input[type=checkbox] + label{
	background-color: #040d1c;
	width: 95px;
	/* height: 80px; */
	border-radius: 50px;
	display: block;
	padding: 10px;
	box-shadow: inset 5px 5px 20px rgba(0,0,0,0.5);
	z-index: 5;
	position: absolute;
	bottom: 30px;
	right: 30px;
	cursor: pointer;
	cursor: hand;
  transition: all 0.5s;
  margin-right: 70px;
}

#a{
	width:30px;
	height:30px;
	border-radius:100px;
	background-color: #f0f0f0;
	box-shadow: 0 0 100px rgba(255,255,255,1); 
  transition: all 0.5s;
}

#c1, #c2, #c3{
  transition: all 0.5s;
	background-color: rgba(0,0,0,0.03);
	width: 10px;
	height: 10px;
	border-radius: 100%;
	position: relative;
}

#c1{
	top: 50px;
	left: 20px
}

#c2{
	top: 50px;
	left: 40px
}

#c3{
	top: 10px;
	left: 40px;
	width: 20px;
	height: 20px;
}

input[type=checkbox]{display: none;}

input[type=checkbox]:checked + label{
  transition: all 0.5s;
	background-color: #2a5fba; 
}

input[type=checkbox]:checked + label #a{
  transition: all 0.5s;
	background-color: #f4e94e; 
	margin-left: calc(100% - 30px);
	box-shadow: 0 0 100px rgba(249, 240, 104,1); 
}

input[type=checkbox]:checked + label #a *{
	opacity: 0;
}

/* 구름 */
.cloud-container{
  opacity: 0.6; position: absolute;
}

.cloud, .cloud::before, .cloud::after{
  border-radius: 35px; background: #fff;
}

.cloud {
  display: inline-block;
  vertical-align: top;
  width: 100px;
  height: 30px;
  border-radius: 50px 35px 35px 15px;
  position: relative;
}

.cloud:before,
.cloud:after {
  content:'';
  display: block;
  position: absolute;
}

.cloud:before {  
  width: 40px;
  height: 45px;
  left: 12px;
  bottom: 0;
}

.cloud:after {
  width: 40px;
  height: 50px;
  right: 16px;
  bottom: 0;
}

.cloud:nth-child(1){
  position: absolute;
  top: 300px;
  left: 400px;
}

.cloud:nth-child(2){
  position: absolute;
  top: 250px;
  left: -150px;
}

.cloud:nth-child(3){
  position: absolute;
  top: 180px;
  left: -450px;
}

.cloud:nth-child(4){
  position: absolute;
  top: 80px;
  left: -230px;
}

.cloud:nth-child(5){
  position: absolute;
  top: 50px;
  left: 230px;
}

.cloud:nth-child(6) {
  position: absolute;
  top: 140px;
  right: 100px;
}

.cloud:nth-child(7){
  position: absolute;
  top: 50px;
  left: -700px;
}

.cloud:nth-child(8){
  position: absolute;
  top: 80px;
  left: -230px;
}

.cloud:nth-child(9){
  position: absolute;
  top: 200px;
  left: 700px;
}

.cloud:nth-child(10) {
  position: absolute;
  top: 300px;
  right: 500px;
}

.cloud {
    -webkit-animation: cloudmove 50s linear infinite both;
            animation: cloudmove 50s linear infinite both;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes cloudmove {
  0% { -webkit-transform: translateX(-450px); transform: translateX(-450px); }
  100% { -webkit-transform: translateX(1090px); transform: translateX(1090px); }
}

@keyframes cloudmove {
  0% { -webkit-transform: translateX(-450px); transform: translateX(-450px); }
  100% { -webkit-transform: translateX(1090px); transform: translateX(1090px); }
}

/* ABOUT */

.line{
  width: 100%; height: 100vh;
  position: relative;
}

.line > div{
  height: 100vh; width: 90%; position: relative; top: 0; left: 50%; -webkit-transform: translateX(-50%); transform: translateX(-50%);
  display: flex; justify-content: space-around; align-items: center;
}

.title{
  font-weight: 200;
  font-size: 70px;
  color: #333;
  line-height: 1.5;
}

.title > strong{
  font-weight: 400 !important;
  color: #111 !important;
}

.gif{
  width: 350px; height: 350px;
  display: flex; justify-content: center; align-items: center;
  position: relative;
}

.mimo{
  width: 500px; height: 500px;
}

.arrow-down{
  position: absolute; bottom: 0%; left: 50%;
}

.arrow-down path {
stroke: #111;
fill: transparent;
stroke-width: 1px;	
animation: arrow 2s infinite;
-webkit-animation: arrow 2s infinite; 
}

@-webkit-keyframes arrow
{
0% {opacity:0}
40% {opacity:.8}
80% {opacity:0}
100% {opacity:0}
}

@keyframes arrow
{
0% {opacity:0}
40% {opacity:.8}
80% {opacity:0}
100% {opacity:0}
}

.arrow-down path.a1 {
animation-delay:-1s;
-webkit-animation-delay:-1s;
}

.arrow-down path.a2 {
animation-delay:-0.5s;
-webkit-animation-delay:-0.5s; 
}

