/* WORK */

.work{
  width: 100%;
}

.work > ul{
  display: flex; flex-wrap: wrap;
}

.work > ul a{
  width: 50%;
}

.work > ul li{
  width: 100%; height: 50vh; background-color: #000;
  position: relative;
  cursor: pointer;
}

.work > ul li img{
  width: 100%; height: 100%;
  object-fit: cover;
}

.work > ul li:before{
  content: ''; display: block;
  background-color: rgba(0,0,0,0.5);
  width: 100%; height: 50vh; position: absolute; top: 0; left: 0;
  transition: 0.5s;
}

.work > ul li:hover:before{
  background-color: rgba(0,0,0,0.8);
  transition: 0.5s;
}

.work > ul li:hover .con{
  top: 55%; -webkit-transform: translateY(-55%); transform: translateY(-55%);
  transition: 0.5s;
  z-index: 555;
}

.work > ul li:hover .con .skill > span:nth-child(1){
  margin-top: -21px;
  transition: 0.5s;
}

.work > ul li .con{
  position: absolute; top: 60%; left: 10%;
  -webkit-transform: translateY(-60%);
          transform: translateY(-60%);
  transition: 0.5s;
}

.con > .con_tit{
  font-size: 50px; font-weight: 400; color: #fff;
