  line-height: 1.5;
  transition: 0.5s;
}

/* DETAIL */

.detail-bg-scroll{
  width: 100%; height: 50vh;
  display: flex; flex-direction: column; justify-content: center; align-items: center;
  color:#fff; font-size: 100px; font-weight: 700; font-family: 'Montserrat', sans-serif;
  transition: 0.5s ease-in-out;
}

.detail-tit{
  color: transparent; -webkit-text-stroke: 2px #fff;
  font-family: 'Rozha One', serif;
  font-size: 80px; font-weight: 400;
  line-height: 1; 
  text-shadow: 5px 5px 10px rgb(0 0 0 / 50%);
  -webkit-animation: detail 1s 0.5s forwards;
          animation: detail 1s 0.5s forwards;
}

@-webkit-keyframes detail {
  0% {
    opacity: 0;
    -webkit-transform: translate(0, 50px);
            transform: translate(0, 50px);
  }
  100%{
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

@keyframes detail {
  0% {
    opacity: 0;
    -webkit-transform: translate(0, 50px);
            transform: translate(0, 50px);
  }
  100%{
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

