  padding-top: 40px; 
}

.row2 > .txt span{
  margin-top: 0;
  width: 60%;
  word-break: normal;
}

/* row3 */

/* row4 */
.row4{
  display: grid;
  margin-bottom: 50px;
  box-shadow: 5px 5px 30px rgb(0 0 0 / 10%);
}

iframe{
  width: 100%;
  min-height: 840px;
  border-radius: 5px;
  border: 1px solid #ddd;
}

.website{
  display: flex; justify-content: center;
  margin-bottom: 120px;
}

.website .about-resume:nth-of-type(3){margin-right: 0;}

/* next */
.next{ 
  width: 100%; height: 400px;
  position: relative;
}

.next-fade > small{
  font-size: 18px; font-weight: 300;
  text-shadow: 5px 5px 10px rgb(0 0 0 / 50%);
  font-family: Montserrat,sans-serif;
}

.next-fade > span{
  font-size: 50px; font-weight: 400;
  padding-top: 20px;
  font-family: Rozha One,serif;
  text-shadow: 5px 5px 10px rgb(0 0 0 / 50%)
}

.next-fade{
  background-color: rgba(0,0,0,0.5); width: 100%; height: 100%;
  font-size: 25px; font-weight: 400; font-family: 'Poppins', sans-serif; color: #fff;
  display: flex; justify-content: center; align-items: center; position: absolute; z-index: 1;
  transition: all 0.5s ease-in-out; text-transform: uppercase;
  cursor: pointer; flex-direction: column;
}

.next-img{
  position: absolute; top: 50%; left: 50%; -webkit-transform: translate(-50%,-50%); transform: translate(-50%,-50%);
  /* background: url(./img/thum2.png) no-repeat center center; */
  width: 100%; height:100%; overflow: hidden;
}

.next-img img{
  width: 100%; height: 100%;
  object-fit: cover;
  object-position: center center;
}

.next:hover .next-fade{
  background-color: transparent;
  opacity: 0.5;
  transition: all 0.5s ease-in-out;
}



.day_bg{
  background: 
  linear-gradient(
    hsl(200, 50%, 80%) 0%, 
    hsl(200, 30%, 95%) 75%);
  ;
}

.balloon{
  background: url(/portfolio2021/static/media/balloon.4f85e0a6.png) no-repeat center center;
  width: 284px; height: 414px;
  position: absolute; top: 15%; left: 75%; cursor: pointer;
  -webkit-animation: animate-ballon 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both, circlemove 3s 1s infinite linear forwards;
          animation: animate-ballon 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both, circlemove 3s 1s infinite linear forwards;
}

@-webkit-keyframes animate-ballon {
  0% {
    -webkit-transform: translate(1000px,-10%);
            transform: translate(1000px,-10%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate(-17%, -10%);
            transform: translate(-17%, -10%);
    opacity: 1;
  }
}

@keyframes animate-ballon {
  0% {
    -webkit-transform: translate(1000px,-10%);
            transform: translate(1000px,-10%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate(-17%, -10%);
            transform: translate(-17%, -10%);
    opacity: 1;
  }
}

@-webkit-keyframes circlemove{ 
  0%{ -webkit-transform:translate(-17%,-10%); transform:translate(-17%,-10%);} 
  50%{ -webkit-transform:translate(-17%,-20%); transform:translate(-17%,-20%);} 
  100%{ -webkit-transform:translate(-17%,-10%); transform:translate(-17%,-10%);}
}

@keyframes circlemove{ 
  0%{ -webkit-transform:translate(-17%,-10%); transform:translate(-17%,-10%);} 
  50%{ -webkit-transform:translate(-17%,-20%); transform:translate(-17%,-20%);} 
  100%{ -webkit-transform:translate(-17%,-10%); transform:translate(-17%,-10%);}
}

.night_bg{
  background: 
    linear-gradient(
      #1b246a 0, #584bb5 30%, #584bb5 31%, #b878a0 100%
      )
}

.moon{
  background: linear-gradient(#fcfaff, #ede4f5);
  height: 300px;
  width: 300px;
  border-radius: 50%;
  position: absolute;
  left: 70%;
  box-shadow: 10px 10px 10px #877dd0,
  -10px 10px 10px #877dd0,
  10px -10px 10px #877dd0,
  -10px -10px 10px #877dd0,
  5px 5px 5px #9585d0,
  -5px 5px 5px #9585d0,
  5px -5px 5px #9585d0,
  -5px -5px 5px #9585d0;
  -webkit-animation: animate-moon 2s forwards;
          animation: animate-moon 2s forwards;
  top: -40%;
  cursor: pointer;
}

@-webkit-keyframes animate-moon {
  100% {
      top: 15%;
  }
}

@keyframes animate-moon {
  100% {
      top: 15%;
  }
}


/* 미디어 쿼리 */
@media (max-width: 768px) {

  /* main */
  input[type=checkbox] + label{margin-right: 10px;}
  .chat{right:60px}
  .main_title{font-size: 65px;}
  .message1{height: 95px;}

  @-webkit-keyframes show_tit {
    0% {margin-top:-0px;}
    5% {margin-top:-95px;}
    33% {margin-top:-95px;}
    38% {margin-top:-190px;}
    66% {margin-top:-190px;}
    71% {margin-top:-285px;}
    99.99% {margin-top:-285px;}
    100% {margin-top:-0px;}
  }

  @keyframes show_tit {
    0% {margin-top:-0px;}
    5% {margin-top:-95px;}
    33% {margin-top:-95px;}
    38% {margin-top:-190px;}
    66% {margin-top:-190px;}
    71% {margin-top:-285px;}
    99.99% {margin-top:-285px;}
    100% {margin-top:-0px;}
  }

  /* menu */
  .hide-menu{width: 100%;}
  
  /* about */
  .line > div{flex-direction: column-reverse; justify-content:center;}
  .mimo{width: 400px; height: 400px;}
  .title{text-align: center; font-size: 65px; margin-top: 5%;}
  .arrow-down{display: none;}
  .star > img:nth-child(1){width: 125px; top: 0; left: -30%;}
  .title > small:nth-child(1){margin-top: 10%;}
  .skill_icons{display: none;}
  .sec3 > div{flex-direction: column !important;}
  .text{padding-left: 0;}
  .skill ul{padding-top: 40px;}
  .line .about-resume{margin-top: 30px;}

  /* footer */
  .con > .con_tit{font-size: 35px;}
  .con > p{font-size: 10px;}

  /* detail */
  .detail-tit{font-size: 50px;}
  .detail-bg-scroll{height: 30vh;}
  .detail{flex-direction: column; align-items: flex-start;}
  .detail > .left, .detail > .right{width: auto;}
  .detail > .right{margin-top: 30px;}
  .txt .txt_tit{margin-top: 20px;}
  .row1{flex-direction: column;}
  .row1 > video{margin-right: 0; width: 100%}
  .row1:nth-of-type(2){flex-direction: column-reverse;}
  .row1 > video{margin-left: 0 !important;}
  .txt{width: auto;}
  .row2 > div{flex-direction: column-reverse;}
  .row2 > div > video{width: 100%;}
  .row2 > div > video:nth-of-type(2){margin-bottom: 20px;}
  .row2 > .txt{padding-top: 0px; flex-direction: column;}
  .row2 > .txt span{width: auto; margin-top: 20px;}
  .fixBg{height: 35vh !important;}
  .next{height: 30vh;}

  /* contact */
  .contact > div{flex-direction: column;}
  .contact > div .title{text-align: left; font-size: 50px;}
  .sec1 .title > small{font-size: 20px;}
  .contact_desc{padding-top: 15%;}
  .contact_desc ul li{flex-direction: row-reverse;}
  .contact_desc ul li p{margin: 5px 10px;}
  .line > div{width: 75%; align-items: flex-start;}
} 


